@import 'src/styles/helpers.scss';

.section {
  position: relative;
  z-index: 0;
  padding: rem(40px) 0;

  border-bottom: 1px solid color(borders);
}

.row {
  display: flex;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;

  svg {
    width: rem(220px);
    height: auto;
  }
}

.title {
  
}

.span {
  color: color(gray-alt);
}

.list {
  margin-top: rem(46px);

  display: flex;

  counter-reset: index;
}

.item {
  counter-increment: index;

  width: calc(#{$col-part-d} * 3);
  display: flex;

  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.text {
  &::before {
    content: "0" counter(index);
    margin-bottom: rem(12px);

    display: block;

    text-align: center;
    color: color(gray-alt);
  }
}

